(function () {
  'use strict';

  angular
    .module('fccLocalWeather', ['ngAnimate', 'ngCookies', 'ngTouch', 'ngSanitize', 'ngMessages', 'ngAria', 'ngResource', 'ngRoute', 'ui.bootstrap']);

})();

(function () {
  'use strict';

  angular
    .module('fccLocalWeather')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController($http, WEATHER_KEY) {
    var vm = this;
    vm.tempF = '90';
    vm.tempC = '90';
    vm.celsius = false;
    vm.city = '';
    vm.country = '';
    vm.lat = '';
    vm.long = '';
    vm.weather = '';
    vm.weatherData = '';
    vm.weatherIcon = '';
    activate();

    function activate() {
      getLocation();

    }

    function getLocation() {
      $http.get('http://ip-api.com/json')
        .success(function (coordinates) {
          vm.city = coordinates.city;
          vm.country = coordinates.country;
          vm.countryCode = coordinates.countryCode;
          getWeather();
        });
    }

    function getWeather() {
      $http.get('http://api.openweathermap.org/data/2.5/weather?&units=metric&q=' + vm.city + ',' + vm.countryCode + '&APPID=' + WEATHER_KEY).success(function (weatherData) {
        vm.weatherData = weatherData;
        vm.weatherIcon = 'http://openweathermap.org/img/w/' + weatherData['weather'][0].icon + '.png';
        vm.weather = weatherData['weather'][0].main;
        vm.tempC = weatherData['main'].temp;
        vm.tempF = (vm.tempC * (9 / 5)) + 32;
      });
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('fccLocalWeather')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log) {

    $log.debug('runBlock end');
  }

})();

(function () {
  'use strict';

  angular
    .module('fccLocalWeather')
    .config(routeConfig);

  function routeConfig($routeProvider) {
    $routeProvider
      .when('/', {
        templateUrl: 'app/main/main.html',
        controller: 'MainController',
        controllerAs: 'vm'
      })
      .otherwise({
        redirectTo: '/'
      });
  }

})();

(function () {
  'use strict';

  angular
    .module('fccLocalWeather')
    .constant('WEATHER_KEY', 'ced22edda5ab59a89469af8a36ce60af');
})();

(function () {
  'use strict';

  angular
    .module('fccLocalWeather')
    .config(config);

  /** @ngInject */
  function config($logProvider, $httpProvider) {
    // Enable log
    $logProvider.debugEnabled(true);
    $httpProvider.defaults.useXDomain = true;
    delete $httpProvider.defaults.headers.common['X-Requested-With'];
    // Set options third-party lib

  }

})();

angular.module("fccLocalWeather").run(["$templateCache", function($templateCache) {$templateCache.put("app/main/main.html","<div class=container><div class=\"jumbotron text-center\"><h1>\'Allo, \'Allo!</h1><p>{{vm.tempF}}</p><p>{{vm.tempC}}</p><p>{{vm.celsius}}</p><p>{{vm.city}}</p><p>{{vm.country}}</p><p>{{vm.countryCode}}</p><p>{{vm.position}}</p><p>{{vm.lat}}</p><p>{{vm.long}}</p><p>{{vm.weather}}</p><img src={{vm.weatherIcon}} alt=\"\"></div></div>");}]);